.crewPage {
    background: url('../assets/crew/background-crew-desktop.jpg') no-repeat top fixed;
    width: 100vw;
    height: 100vh;
    border: 1px transparent solid;
    background-size: cover;
    overflow: hidden;

    .crewContent {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        margin-top: auto;
        overflow: hidden;
        margin: auto;
        width: 80%;
        display: flex;
        justify-content: space-between;


        .crewInfo {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            padding-right: 3rem;

            .tagLine {
                font-family: 'Barlow Condensed';
                font-style: normal;
                font-weight: 400;
                font-size: 28px;
                line-height: 34px;
                color: white;
                letter-spacing: 4.725px;

                .tag {
                    margin-right: 1rem;
                    color: rgb(80, 80, 80);
                }
            }

            .info {
                margin-top: 10%;

                .post {
                    color: white;
                    font-family: "Bellefair", sans-serif;
                    font-size: 1.5rem;
                }

                .name {
                    margin-top: 1.2rem;
                    color: white;
                    font-family: "Bellefair", sans-serif;
                    font-size: 2.3rem;
                }

                article {
                    width: 70%;
                    margin-top: 1rem;

                    .about {
                        font-family: "Barlow Condensed", sans-serif;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 32px;
                        color: white;
                    }
                }

            }

            .selectionButtons {
                position: fixed;
                bottom: 2rem;
                width: 10rem;
                display: flex;
                justify-content: space-between;

                label {
                    width: 1rem;
                    height: 1rem;
                    border: 1px white solid;
                    border-radius: 100%;
                    cursor: pointer;
                    transition: all 300ms ease-in-out;

                    &:hover {
                        background-color: gray;
                    }
                }

                input {
                    display: none;
                }

                .active {
                    background-color: white;
                }
            }
        }


        .crewManPicture {
            display: flex;
            height: 30rem;
            width: 40rem;

            img {
                width: 100%;
            }
        }
    }
}

@media only screen and (max-width: 900px) {
    .crewPage {
        background-image: url('../assets/crew/background-crew-tablet.jpg');

        .crewContent {
            flex-direction: column;
            width: 100%;
            gap: 12rem;

            .crewInfo {

                .tagLine {
                    padding: 0 2rem;
                    margin-right: auto;
                }

                padding: 0 1rem;
                text-align: center;
                margin: auto;

                .info {
                    .name {
                        font-size: 3rem;
                    }

                    article {
                        width: 70%;
                        margin: 2rem auto;

                        .about {
                            margin: auto;
                        }
                    }
                }

                .selectionButtons {
                    top: 45%;
                    left: 0;
                    right: 0;
                    margin: auto;
                    bottom: 0;
                }

            }

            .crewManPicture {
                margin: auto;
                width: 28rem;
                height: 35rem;
            }
        }
    }
}


@media only screen and (max-width: 400px) {
    .crewPage {
        background-image: url('../assets/crew/background-crew-mobile.jpg');
        overflow: visible;
        height: 100%;

        .crewContent {
            flex-direction: column-reverse;
            margin-top: 25%;
            position: relative;



            .crewManPicture {
                margin-top: 20%;
                width: 15rem;
                height: 20rem;
                border-bottom: 1px gray solid;
            }

            .crewInfo {
                padding: 0;
                margin-top: -25%;

                .selectionButtons {
                    top: 53%;
                    position: absolute;
                }

                .tagLine {
                    top: 0rem;
                    position: absolute;
                    font-size: 1rem;
                    text-align: center;
                    width: 100%;
                    margin-right: 0;
                    padding: 0;
                }

                .info {
                    margin-top: 0;

                    .post {
                        font-size: 1.2rem;
                    }

                    .name {
                        font-size: 2rem;
                    }

                    article {
                        width: 100%;
                        padding: 0 1rem;

                        .about {
                            font-size: 1rem;
                        }
                    }
                }

            }

            hr {
                display: none;
            }
        }
    }
}