.destinationsPage {
    background: url("../assets/destination/background-destination-desktop.jpg") no-repeat top fixed;
    width: 100vw;
    height: 100vh;
    border: 1px transparent solid;
    background-size: cover;

    .destination {
        overflow: hidden;
        margin: 12% auto 0;
        width: 80%;
        display: flex;

        .imageS {
            width: 60%;
            display: flex;
            flex-direction: column;
            gap: 4rem;

            .tagLine {
                font-family: 'Barlow Condensed';
                font-style: normal;
                font-weight: 400;
                font-size: 28px;
                line-height: 34px;
                color: white;
                letter-spacing: 4.725px;

                .tag {
                    margin-right: 1rem;
                    color: rgb(80, 80, 80);
                }
            }

            .imageCont {
                margin: auto;
                width: 21rem;
                height: 21rem;

                img {
                    width: 100%;
                    transition: transform 2s ease-in-out;

                    &:hover {
                        transform: rotate(180deg);
                    }
                }
            }
        }

        .infoDev {
            margin-top: 2rem;
            width: 40%;
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .destNav {
                display: flex;
                justify-content: space-between;


                label {
                    color: gray;
                    font-family: "Barlow Condensed", sans-serif;
                    font-size: 1.2rem;
                    position: relative;
                    cursor: pointer;

                    input {
                        display: none;
                    }
                }

                .active {
                    color: white;

                    &::after {
                        content: '';
                        position: absolute;
                        bottom: -70%;
                        left: 0;
                        background-color: white;
                        height: 2px;
                        width: 100%;
                    }
                }
            }

            .planetInfo {
                margin-top: 2rem;

                .planetName {
                    font-size: 5rem;
                    font-family: 'Bellefair', sans-serif;
                    color: white;
                }

                .aboutPlanet {
                    p {
                        font-family: "Barlow Condensed", sans-serif;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 32px;
                        color: white;
                    }
                }

                hr {
                    margin: 1rem 0;
                }

                aside {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    .distance,
                    .time {
                        display: flex;
                        flex-direction: column;
                        width: 30%;
                        gap: 1rem;
                    }

                    div {
                        span {
                            color: white;
                        }

                        .head {
                            font-size: 1rem;
                            font-family: 'Barlow Condensed', sans-serif;
                        }

                        .data {
                            font-family: 'Bellefair', sans-serif;
                            font-size: 1.5rem;
                        }
                    }
                }
            }
        }
    }
}


@media only screen and (max-width: 900px) {
    .destinationsPage {
        background-image: url("../assets/destination/background-destination-tablet.jpg");

        .destination {
            flex-direction: column;
            width: 100%;
            align-items: center;
            margin-top: 18%;

            .imageS {
                width: 90%;

                .tagLine {
                    width: 100%;
                    text-align: left;
                    margin-bottom: 3rem;
                }
            }

            .infoDev {
                margin-top: 5rem;
                width: 80%;

                .destNav {
                    margin: auto;
                    width: 60%;
                }

                .planetInfo {
                    margin-top: 4rem;
                    text-align: center;
                    width: 100%;

                    .planetName {
                        font-size: 6rem;
                    }

                    .aboutPlanet {
                        width: 80%;
                        margin: 2rem auto;

                        p {
                            font-size: 1.2rem;
                        }
                    }

                    hr {
                        margin: 5rem 0 2rem 0;
                    }

                    aside {
                        div {
                            gap: 2rem;

                            .head {
                                font-size: 1.5rem;
                            }

                            .data {
                                font-size: 2rem;
                            }
                        }
                    }
                }
            }
        }
    }
}


@media only screen and (max-width: 400px) {
    .destinationsPage {
        background-image: url('../assets/destination/background-destination-mobile.jpg');
        height: 100%;

        .destination {
            margin: 28% auto 0;

            .imageS {
                gap: 1rem;

                .tagLine {
                    text-align: center;
                    margin-bottom: 1.5rem;
                    font-size: 1rem;
                }

                .imageCont {
                    width: 18rem;
                    height: 18rem;
                }

            }

            .infoDev {
                margin-top: 3rem;
                width: 100%;

                .destNav {
                    gap: 1.5rem;
                    margin: auto;
                }

                .planetInfo {
                    margin-top: 1.8rem;

                    .planetName {
                        font-size: 4.2rem;
                    }

                    .aboutPlanet {
                        margin-top: 1rem;

                        p {
                            font-size: 1rem;
                        }


                    }

                    hr {
                        width: 90%;
                        margin: 1rem auto 2rem;
                    }

                    aside {
                        margin-bottom: 1rem;
                        flex-direction: column;
                        width: 100%;
                        gap: 3rem;

                        .distance,
                        .time {
                            width: 100%;
                            gap: 1rem;

                            .head {
                                font-size: 1.5rem;
                            }

                            .data {
                                font-size: 2rem;
                            }
                        }
                    }
                }
            }
        }
    }
}