    .technologiesPage {
        width: 100vw;
        height: 100vh;
        border: 1px transparent solid;
        background: url('../assets/technology/background-technology-desktop.jpg') no-repeat top fixed;
        background-size: cover;

        .technologyContent {
            margin: 12% auto 0;
            display: flex;
            width: 80%;
            justify-content: space-between;

            .info {
                width: 50%;
                display: flex;
                flex-direction: column;
                gap: 4rem;

                .tagLine {
                    font-family: 'Barlow Condensed';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 28px;
                    line-height: 34px;
                    color: white;
                    letter-spacing: 4.725px;

                    .tag {
                        margin-right: 1rem;
                        color: rgb(80, 80, 80);
                    }
                }

                .content {
                    display: flex;
                    gap: 5rem;

                    aside {
                        display: flex;
                        flex-direction: column;
                        gap: 3rem;

                        label {
                            width: 4rem;
                            height: 4rem;
                            border: 1px white solid;
                            border-radius: 100%;
                            color: white;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            cursor: pointer;
                            transition: all 400ms ease-in-out;
                            font-size: 1.1rem;
                            font-family: "Bellefair", sans-serif;

                            &:hover {
                                border: 2px white solid;
                            }
                        }

                        input {
                            display: none;
                        }

                        .active {
                            background-color: white;
                            color: black;
                        }
                    }

                    .techInfo {
                        display: flex;
                        flex-direction: column;
                        gap: 1rem;

                        span {
                            font-family: "Barlow Condensed", sans-serif;
                            color: white;
                            letter-spacing: 2px;
                            word-spacing: 4px;
                            font-size: 1rem;
                        }

                        .name {
                            margin-top: 1.2rem;
                            color: white;
                            font-family: "Bellefair", sans-serif;
                            font-size: 2.3rem;
                        }

                        article {
                            .about {
                                font-family: "Barlow Condensed", sans-serif;
                                font-style: normal;
                                font-weight: 400;
                                font-size: 18px;
                                line-height: 32px;
                                color: white;
                            }
                        }
                    }
                }

            }

            .imageCont {
                width: 25rem;
                height: 27rem;

                img {
                    width: 100%;
                }

                .desktop {
                    display: block;
                }

                .mobile {
                    display: none;
                }
            }

        }
    }



    @media only screen and (max-width: 900px) {
        .technologiesPage {
            background-image: url("../assets/technology/background-technology-tablet.jpg");

            .technologyContent {
                width: 100%;
                margin: 25% auto 0;
                flex-direction: column-reverse;
                align-items: center;

                .info {
                    .tagLine {
                        top: 10%;
                        padding: 0 2rem;

                        width: 100%;
                        left: 0;
                        position: absolute;
                    }


                    .content {
                        margin-top: 15%;
                        text-align: center;
                        flex-direction: column;

                        aside {

                            gap: 5rem;
                            justify-content: center;
                            flex-direction: row;
                        }

                        .techInfo {
                            span {
                                font-size: 1.2rem;
                            }

                            .name {
                                font-size: 4rem;
                            }
                        }
                    }
                }

                .imageCont {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .desktop {
                        display: none;
                    }

                    .mobile {
                        margin: auto;
                        display: block;
                        min-width: 100%;
                    }
                }
            }
        }
    }

    @media only screen and (max-width: 400px) {
        .technologiesPage {
            background-image: url('../assets/technology/background-technology-mobile.jpg');
            height: 100%;

            .technologyContent {
                margin-top: 25%;
                width: 100%;


                .info {
                    width: 100%;
                    gap: 2rem;


                    .tagLine {
                        font-size: 1rem;
                        width: 100%;
                        top: 15%;
                        text-align: center;
                    }

                    .content {
                        padding: 1rem 0;
                        gap: 2rem;
                        width: 100%;

                        aside {
                            gap: 1.5rem;
                            width: 100%;


                            label {
                                width: 3rem;
                                height: 3rem;
                            }
                        }

                        .techInfo {
                            span {
                                font-size: 1rem;
                            }

                            .name {
                                font-size: 2.2rem;
                            }

                            article {
                                padding: 0 1rem;

                                .about {
                                    font-size: 1rem;
                                }
                            }
                        }
                    }


                }

                .imageCont {
                    margin-top: 35%;
                    height: 100%;
                }
            }
        }
    }