.destinationsPage {
  background: url("../assets/destination/background-destination-desktop.jpg") no-repeat top fixed;
  width: 100vw;
  height: 100vh;
  border: 1px transparent solid;
  background-size: cover;
}

.destinationsPage .destination {
  overflow: hidden;
  margin: 12% auto 0;
  width: 80%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.destinationsPage .destination .imageS {
  width: 60%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 4rem;
}

.destinationsPage .destination .imageS .tagLine {
  font-family: 'Barlow Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  color: white;
  letter-spacing: 4.725px;
}

.destinationsPage .destination .imageS .tagLine .tag {
  margin-right: 1rem;
  color: #505050;
}

.destinationsPage .destination .imageS .imageCont {
  margin: auto;
  width: 21rem;
  height: 21rem;
}

.destinationsPage .destination .imageS .imageCont img {
  width: 100%;
  -webkit-transition: -webkit-transform 2s ease-in-out;
  transition: -webkit-transform 2s ease-in-out;
  transition: transform 2s ease-in-out;
  transition: transform 2s ease-in-out, -webkit-transform 2s ease-in-out;
}

.destinationsPage .destination .imageS .imageCont img:hover {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.destinationsPage .destination .infoDev {
  margin-top: 2rem;
  width: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 1rem;
}

.destinationsPage .destination .infoDev .destNav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.destinationsPage .destination .infoDev .destNav label {
  color: gray;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 1.2rem;
  position: relative;
  cursor: pointer;
}

.destinationsPage .destination .infoDev .destNav label input {
  display: none;
}

.destinationsPage .destination .infoDev .destNav .active {
  color: white;
}

.destinationsPage .destination .infoDev .destNav .active::after {
  content: '';
  position: absolute;
  bottom: -70%;
  left: 0;
  background-color: white;
  height: 2px;
  width: 100%;
}

.destinationsPage .destination .infoDev .planetInfo {
  margin-top: 2rem;
}

.destinationsPage .destination .infoDev .planetInfo .planetName {
  font-size: 5rem;
  font-family: 'Bellefair', sans-serif;
  color: white;
}

.destinationsPage .destination .infoDev .planetInfo .aboutPlanet p {
  font-family: "Barlow Condensed", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: white;
}

.destinationsPage .destination .infoDev .planetInfo hr {
  margin: 1rem 0;
}

.destinationsPage .destination .infoDev .planetInfo aside {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.destinationsPage .destination .infoDev .planetInfo aside .distance,
.destinationsPage .destination .infoDev .planetInfo aside .time {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 30%;
  gap: 1rem;
}

.destinationsPage .destination .infoDev .planetInfo aside div span {
  color: white;
}

.destinationsPage .destination .infoDev .planetInfo aside div .head {
  font-size: 1rem;
  font-family: 'Barlow Condensed', sans-serif;
}

.destinationsPage .destination .infoDev .planetInfo aside div .data {
  font-family: 'Bellefair', sans-serif;
  font-size: 1.5rem;
}

@media only screen and (max-width: 900px) {
  .destinationsPage {
    background-image: url("../assets/destination/background-destination-tablet.jpg");
  }
  .destinationsPage .destination {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-top: 18%;
  }
  .destinationsPage .destination .imageS {
    width: 90%;
  }
  .destinationsPage .destination .imageS .tagLine {
    width: 100%;
    text-align: left;
    margin-bottom: 3rem;
  }
  .destinationsPage .destination .infoDev {
    margin-top: 5rem;
    width: 80%;
  }
  .destinationsPage .destination .infoDev .destNav {
    margin: auto;
    width: 60%;
  }
  .destinationsPage .destination .infoDev .planetInfo {
    margin-top: 4rem;
    text-align: center;
    width: 100%;
  }
  .destinationsPage .destination .infoDev .planetInfo .planetName {
    font-size: 6rem;
  }
  .destinationsPage .destination .infoDev .planetInfo .aboutPlanet {
    width: 80%;
    margin: 2rem auto;
  }
  .destinationsPage .destination .infoDev .planetInfo .aboutPlanet p {
    font-size: 1.2rem;
  }
  .destinationsPage .destination .infoDev .planetInfo hr {
    margin: 5rem 0 2rem 0;
  }
  .destinationsPage .destination .infoDev .planetInfo aside div {
    gap: 2rem;
  }
  .destinationsPage .destination .infoDev .planetInfo aside div .head {
    font-size: 1.5rem;
  }
  .destinationsPage .destination .infoDev .planetInfo aside div .data {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 400px) {
  .destinationsPage {
    background-image: url("../assets/destination/background-destination-mobile.jpg");
    height: 100%;
  }
  .destinationsPage .destination {
    margin: 28% auto 0;
  }
  .destinationsPage .destination .imageS {
    gap: 1rem;
  }
  .destinationsPage .destination .imageS .tagLine {
    text-align: center;
    margin-bottom: 1.5rem;
    font-size: 1rem;
  }
  .destinationsPage .destination .imageS .imageCont {
    width: 18rem;
    height: 18rem;
  }
  .destinationsPage .destination .infoDev {
    margin-top: 3rem;
    width: 100%;
  }
  .destinationsPage .destination .infoDev .destNav {
    gap: 1.5rem;
    margin: auto;
  }
  .destinationsPage .destination .infoDev .planetInfo {
    margin-top: 1.8rem;
  }
  .destinationsPage .destination .infoDev .planetInfo .planetName {
    font-size: 4.2rem;
  }
  .destinationsPage .destination .infoDev .planetInfo .aboutPlanet {
    margin-top: 1rem;
  }
  .destinationsPage .destination .infoDev .planetInfo .aboutPlanet p {
    font-size: 1rem;
  }
  .destinationsPage .destination .infoDev .planetInfo hr {
    width: 90%;
    margin: 1rem auto 2rem;
  }
  .destinationsPage .destination .infoDev .planetInfo aside {
    margin-bottom: 1rem;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
    gap: 3rem;
  }
  .destinationsPage .destination .infoDev .planetInfo aside .distance,
  .destinationsPage .destination .infoDev .planetInfo aside .time {
    width: 100%;
    gap: 1rem;
  }
  .destinationsPage .destination .infoDev .planetInfo aside .distance .head,
  .destinationsPage .destination .infoDev .planetInfo aside .time .head {
    font-size: 1.5rem;
  }
  .destinationsPage .destination .infoDev .planetInfo aside .distance .data,
  .destinationsPage .destination .infoDev .planetInfo aside .time .data {
    font-size: 2rem;
  }
}
