.homepage {
  background: url("../assets/home/background-home-desktop.jpg") no-repeat top fixed;
  background-size: cover;
  border: 1px transparent solid;
  height: 100vh;
  width: 100vw;
}

.homepage .heroContent {
  margin: 15% auto 0;
  width: 100%;
  padding: 0 8rem 0 4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.homepage .heroContent .heroText {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 1rem;
  width: 60%;
}

.homepage .heroContent .heroText .tagLine {
  font-family: 'Barlow Condensed', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
  color: #D0D6F9;
}

.homepage .heroContent .heroText .heroHead {
  font-family: 'Bellefair', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 150px;
  line-height: 172px;
  color: #FFFFFF;
}

.homepage .heroContent .heroText .brief {
  font-family: 'Barlow', sans-serif;
  font-style: normal;
  font-size: 1.1rem;
  width: 70%;
  line-height: 32px;
  color: #D0D6F9;
}

.homepage .heroContent .buttonCont {
  width: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  z-index: 1;
  position: relative;
}

.homepage .heroContent .buttonCont .heroButton {
  border: none;
  background-color: white;
  font-family: "Bellefair", sans-serif;
  font-size: 2.4rem;
  width: 15rem;
  height: 15rem;
  margin-right: 2rem;
  border-radius: 100%;
  position: relative;
  margin-bottom: 1rem;
  z-index: 1;
  cursor: pointer;
}

.homepage .heroContent .buttonCont .heroButton::after {
  content: '';
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
  z-index: -1;
  -webkit-transition: opacity 400ms ease-in, -webkit-transform 200ms ease-in-out;
  transition: opacity 400ms ease-in, -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, opacity 400ms ease-in;
  transition: transform 200ms ease-in-out, opacity 400ms ease-in, -webkit-transform 200ms ease-in-out;
}

.homepage .heroContent .buttonCont .heroButton:hover::after {
  opacity: 1;
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}

@media only screen and (max-width: 950px) {
  .homepage {
    background-image: url("../assets/home/background-home-tablet.jpg");
  }
  .homepage .heroContent {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    gap: 20rem;
    padding: 3rem 0;
  }
  .homepage .heroContent .heroText {
    width: 100%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
  }
  .homepage .heroContent .heroText .tagLine {
    font-size: 1.5rem;
  }
  .homepage .heroContent .heroText .brief {
    margin: auto;
    width: 60%;
    font-size: 1rem;
  }
  .homepage .heroContent .buttonCont {
    padding: 0;
    width: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .homepage .heroContent .buttonCont .heroButton {
    margin: 0;
  }
}

@media only screen and (max-width: 400px) {
  .homepage {
    background-image: url("../assets/home/background-home-mobile.jpg");
  }
  .homepage .heroContent {
    margin-top: 20%;
    gap: 6rem;
    padding: 4rem 0;
  }
  .homepage .heroContent .heroText {
    gap: 0rem;
  }
  .homepage .heroContent .heroText .tagLine {
    font-size: 1rem;
    line-height: 1rem;
    letter-spacing: 4px;
  }
  .homepage .heroContent .heroText .heroHead {
    margin-top: -1.3rem;
    font-size: 5rem;
    letter-spacing: 4px;
  }
  .homepage .heroContent .heroText .brief {
    width: 90%;
    font-size: 1rem;
    margin-top: -1rem;
    line-height: 1.8rem;
  }
  .homepage .heroContent .buttonCont .heroButton {
    position: relative;
    width: 12rem;
    height: 12rem;
    font-size: 2rem;
  }
}
