.crewPage {
  background: url("../assets/crew/background-crew-desktop.jpg") no-repeat top fixed;
  width: 100vw;
  height: 100vh;
  border: 1px transparent solid;
  background-size: cover;
  overflow: hidden;
}

.crewPage .crewContent {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-top: auto;
  overflow: hidden;
  margin: auto;
  width: 80%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.crewPage .crewContent .crewInfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 1rem;
  padding-right: 3rem;
}

.crewPage .crewContent .crewInfo .tagLine {
  font-family: 'Barlow Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  color: white;
  letter-spacing: 4.725px;
}

.crewPage .crewContent .crewInfo .tagLine .tag {
  margin-right: 1rem;
  color: #505050;
}

.crewPage .crewContent .crewInfo .info {
  margin-top: 10%;
}

.crewPage .crewContent .crewInfo .info .post {
  color: white;
  font-family: "Bellefair", sans-serif;
  font-size: 1.5rem;
}

.crewPage .crewContent .crewInfo .info .name {
  margin-top: 1.2rem;
  color: white;
  font-family: "Bellefair", sans-serif;
  font-size: 2.3rem;
}

.crewPage .crewContent .crewInfo .info article {
  width: 70%;
  margin-top: 1rem;
}

.crewPage .crewContent .crewInfo .info article .about {
  font-family: "Barlow Condensed", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: white;
}

.crewPage .crewContent .crewInfo .selectionButtons {
  position: fixed;
  bottom: 2rem;
  width: 10rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.crewPage .crewContent .crewInfo .selectionButtons label {
  width: 1rem;
  height: 1rem;
  border: 1px white solid;
  border-radius: 100%;
  cursor: pointer;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.crewPage .crewContent .crewInfo .selectionButtons label:hover {
  background-color: gray;
}

.crewPage .crewContent .crewInfo .selectionButtons input {
  display: none;
}

.crewPage .crewContent .crewInfo .selectionButtons .active {
  background-color: white;
}

.crewPage .crewContent .crewManPicture {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 30rem;
  width: 40rem;
}

.crewPage .crewContent .crewManPicture img {
  width: 100%;
}

@media only screen and (max-width: 900px) {
  .crewPage {
    background-image: url("../assets/crew/background-crew-tablet.jpg");
  }
  .crewPage .crewContent {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
    gap: 12rem;
  }
  .crewPage .crewContent .crewInfo {
    padding: 0 1rem;
    text-align: center;
    margin: auto;
  }
  .crewPage .crewContent .crewInfo .tagLine {
    padding: 0 2rem;
    margin-right: auto;
  }
  .crewPage .crewContent .crewInfo .info .name {
    font-size: 3rem;
  }
  .crewPage .crewContent .crewInfo .info article {
    width: 70%;
    margin: 2rem auto;
  }
  .crewPage .crewContent .crewInfo .info article .about {
    margin: auto;
  }
  .crewPage .crewContent .crewInfo .selectionButtons {
    top: 45%;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 0;
  }
  .crewPage .crewContent .crewManPicture {
    margin: auto;
    width: 28rem;
    height: 35rem;
  }
}

@media only screen and (max-width: 400px) {
  .crewPage {
    background-image: url("../assets/crew/background-crew-mobile.jpg");
    overflow: visible;
    height: 100%;
  }
  .crewPage .crewContent {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    margin-top: 25%;
    position: relative;
  }
  .crewPage .crewContent .crewManPicture {
    margin-top: 20%;
    width: 15rem;
    height: 20rem;
    border-bottom: 1px gray solid;
  }
  .crewPage .crewContent .crewInfo {
    padding: 0;
    margin-top: -25%;
  }
  .crewPage .crewContent .crewInfo .selectionButtons {
    top: 53%;
    position: absolute;
  }
  .crewPage .crewContent .crewInfo .tagLine {
    top: 0rem;
    position: absolute;
    font-size: 1rem;
    text-align: center;
    width: 100%;
    margin-right: 0;
    padding: 0;
  }
  .crewPage .crewContent .crewInfo .info {
    margin-top: 0;
  }
  .crewPage .crewContent .crewInfo .info .post {
    font-size: 1.2rem;
  }
  .crewPage .crewContent .crewInfo .info .name {
    font-size: 2rem;
  }
  .crewPage .crewContent .crewInfo .info article {
    width: 100%;
    padding: 0 1rem;
  }
  .crewPage .crewContent .crewInfo .info article .about {
    font-size: 1rem;
  }
  .crewPage .crewContent hr {
    display: none;
  }
}
