nav {
    position: fixed;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2rem;


    .mainLogo {
        padding-left: 4rem;

        .logo {
            transition: all 800ms ease-in;

            &:hover {
                transform: rotateZ(360deg);
            }
        }

    }

    .midline {
        left: 12%;
        width: 35%;
        position: absolute;
        z-index: 20;
        height: 1px;
        background-color: #979797;
    }

    .openMenu {
        display: none;
    }

    .leftContainer {
        position: relative;
        z-index: 500;
        // padding: 1.6rem 8rem 1.6rem 3rem;
        width: 56%;
        padding: 1.6rem 0;
        background: rgba(255, 255, 255, 0.04);
        backdrop-filter: blur(40.7742px);


        .closeMenu {
            display: none
        }

        ul {
            margin-left: 5rem;
            display: flex;
            gap: 3rem;

            li {
                list-style-type: none;
                height: 100%;

                a {
                    position: relative;
                    color: rgb(175, 175, 175);
                    text-decoration: none;
                    font-family: "Barlow Condensed";
                    font-size: 1.2rem;
                    letter-spacing: 2.3px;
                    font-weight: 400;
                    transition: all 200ms ease-in;


                    &::after {
                        content: '';
                        position: absolute;
                        height: 3px;
                        width: 100%;
                        background-color: rgb(175, 175, 175);
                        left: 0;
                        bottom: -110%;
                        opacity: 0;
                        transition: all 200ms ease-in;
                    }

                    &:hover::after {
                        opacity: 1;
                    }

                    &:active {
                        color: white;

                        &::after {
                            background-color: white;
                        }
                    }
                }



            }
        }
    }
}




@media only screen and (max-width: 400px) {
    nav {

        .mainLogo {
            padding-top: 1rem;

            .logo {
                width: 2rem;
            }
        }

        .openMenu {
            display: block;
            margin-top: 2%;
            margin-right: 10%;
            cursor: pointer;
        }

        .leftContainer {
            display: none;
            height: 100%;
            position: fixed;
            right: 0;
            top: 0;
            width: 70%;
            z-index: 200 !important;

            .closeMenu {
                display: block;
                cursor: pointer;
            }

            .closeMenu {
                margin-top: -2%;
                margin-left: 80%;
            }

            .navLinks {
                flex-direction: column;
                margin-top: 20%;
                margin-left: 1rem;
                gap: 4rem;

                li {
                    a {
                        font-size: 1.2rem;
                    }
                }
            }

        }
    }
}



@media only screen and (max-width: 900px) {
    nav {
        margin: 0;
        z-index: 400;

        .mainLogo {
            padding-left: 2rem;
            padding-top: .5rem;

            .logo {
                width: 2.5rem;
            }
        }

        .midline {
            display: none;
        }

        .leftContainer {
            display: block;
            width: 65%;
            padding: 1.5rem .5rem;

            ul {
                width: 100%;
                margin-left: .8rem;
                gap: 2rem;

                li {


                    a {
                        font-size: .9rem;

                        &::after {
                            bottom: -140%;
                        }
                    }
                }
            }
        }
    }
}