nav {
  position: fixed;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 2rem;
}

nav .mainLogo {
  padding-left: 4rem;
}

nav .mainLogo .logo {
  -webkit-transition: all 800ms ease-in;
  transition: all 800ms ease-in;
}

nav .mainLogo .logo:hover {
  -webkit-transform: rotateZ(360deg);
          transform: rotateZ(360deg);
}

nav .midline {
  left: 12%;
  width: 35%;
  position: absolute;
  z-index: 20;
  height: 1px;
  background-color: #979797;
}

nav .openMenu {
  display: none;
}

nav .leftContainer {
  position: relative;
  z-index: 500;
  width: 56%;
  padding: 1.6rem 0;
  background: rgba(255, 255, 255, 0.04);
  -webkit-backdrop-filter: blur(40.7742px);
          backdrop-filter: blur(40.7742px);
}

nav .leftContainer .closeMenu {
  display: none;
}

nav .leftContainer ul {
  margin-left: 5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 3rem;
}

nav .leftContainer ul li {
  list-style-type: none;
  height: 100%;
}

nav .leftContainer ul li a {
  position: relative;
  color: #afafaf;
  text-decoration: none;
  font-family: "Barlow Condensed";
  font-size: 1.2rem;
  letter-spacing: 2.3px;
  font-weight: 400;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
}

nav .leftContainer ul li a::after {
  content: '';
  position: absolute;
  height: 3px;
  width: 100%;
  background-color: #afafaf;
  left: 0;
  bottom: -110%;
  opacity: 0;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
}

nav .leftContainer ul li a:hover::after {
  opacity: 1;
}

nav .leftContainer ul li a:active {
  color: white;
}

nav .leftContainer ul li a:active::after {
  background-color: white;
}

@media only screen and (max-width: 400px) {
  nav .mainLogo {
    padding-top: 1rem;
  }
  nav .mainLogo .logo {
    width: 2rem;
  }
  nav .openMenu {
    display: block;
    margin-top: 2%;
    margin-right: 10%;
    cursor: pointer;
  }
  nav .leftContainer {
    display: none;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    width: 70%;
    z-index: 200 !important;
  }
  nav .leftContainer .closeMenu {
    display: block;
    cursor: pointer;
  }
  nav .leftContainer .closeMenu {
    margin-top: -2%;
    margin-left: 80%;
  }
  nav .leftContainer .navLinks {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin-top: 20%;
    margin-left: 1rem;
    gap: 4rem;
  }
  nav .leftContainer .navLinks li a {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 900px) {
  nav {
    margin: 0;
    z-index: 400;
  }
  nav .mainLogo {
    padding-left: 2rem;
    padding-top: .5rem;
  }
  nav .mainLogo .logo {
    width: 2.5rem;
  }
  nav .midline {
    display: none;
  }
  nav .leftContainer {
    display: block;
    width: 65%;
    padding: 1.5rem .5rem;
  }
  nav .leftContainer ul {
    width: 100%;
    margin-left: .8rem;
    gap: 2rem;
  }
  nav .leftContainer ul li a {
    font-size: .9rem;
  }
  nav .leftContainer ul li a::after {
    bottom: -140%;
  }
}
