.technologiesPage {
  width: 100vw;
  height: 100vh;
  border: 1px transparent solid;
  background: url("../assets/technology/background-technology-desktop.jpg") no-repeat top fixed;
  background-size: cover;
}

.technologiesPage .technologyContent {
  margin: 12% auto 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.technologiesPage .technologyContent .info {
  width: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 4rem;
}

.technologiesPage .technologyContent .info .tagLine {
  font-family: 'Barlow Condensed';
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  color: white;
  letter-spacing: 4.725px;
}

.technologiesPage .technologyContent .info .tagLine .tag {
  margin-right: 1rem;
  color: #505050;
}

.technologiesPage .technologyContent .info .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 5rem;
}

.technologiesPage .technologyContent .info .content aside {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 3rem;
}

.technologiesPage .technologyContent .info .content aside label {
  width: 4rem;
  height: 4rem;
  border: 1px white solid;
  border-radius: 100%;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  -webkit-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;
  font-size: 1.1rem;
  font-family: "Bellefair", sans-serif;
}

.technologiesPage .technologyContent .info .content aside label:hover {
  border: 2px white solid;
}

.technologiesPage .technologyContent .info .content aside input {
  display: none;
}

.technologiesPage .technologyContent .info .content aside .active {
  background-color: white;
  color: black;
}

.technologiesPage .technologyContent .info .content .techInfo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 1rem;
}

.technologiesPage .technologyContent .info .content .techInfo span {
  font-family: "Barlow Condensed", sans-serif;
  color: white;
  letter-spacing: 2px;
  word-spacing: 4px;
  font-size: 1rem;
}

.technologiesPage .technologyContent .info .content .techInfo .name {
  margin-top: 1.2rem;
  color: white;
  font-family: "Bellefair", sans-serif;
  font-size: 2.3rem;
}

.technologiesPage .technologyContent .info .content .techInfo article .about {
  font-family: "Barlow Condensed", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: white;
}

.technologiesPage .technologyContent .imageCont {
  width: 25rem;
  height: 27rem;
}

.technologiesPage .technologyContent .imageCont img {
  width: 100%;
}

.technologiesPage .technologyContent .imageCont .desktop {
  display: block;
}

.technologiesPage .technologyContent .imageCont .mobile {
  display: none;
}

@media only screen and (max-width: 900px) {
  .technologiesPage {
    background-image: url("../assets/technology/background-technology-tablet.jpg");
  }
  .technologiesPage .technologyContent {
    width: 100%;
    margin: 25% auto 0;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  .technologiesPage .technologyContent .info .tagLine {
    top: 10%;
    padding: 0 2rem;
    width: 100%;
    left: 0;
    position: absolute;
  }
  .technologiesPage .technologyContent .info .content {
    margin-top: 15%;
    text-align: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .technologiesPage .technologyContent .info .content aside {
    gap: 5rem;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
  }
  .technologiesPage .technologyContent .info .content .techInfo span {
    font-size: 1.2rem;
  }
  .technologiesPage .technologyContent .info .content .techInfo .name {
    font-size: 4rem;
  }
  .technologiesPage .technologyContent .imageCont {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .technologiesPage .technologyContent .imageCont .desktop {
    display: none;
  }
  .technologiesPage .technologyContent .imageCont .mobile {
    margin: auto;
    display: block;
    min-width: 100%;
  }
}

@media only screen and (max-width: 400px) {
  .technologiesPage {
    background-image: url("../assets/technology/background-technology-mobile.jpg");
    height: 100%;
  }
  .technologiesPage .technologyContent {
    margin-top: 25%;
    width: 100%;
  }
  .technologiesPage .technologyContent .info {
    width: 100%;
    gap: 2rem;
  }
  .technologiesPage .technologyContent .info .tagLine {
    font-size: 1rem;
    width: 100%;
    top: 15%;
    text-align: center;
  }
  .technologiesPage .technologyContent .info .content {
    padding: 1rem 0;
    gap: 2rem;
    width: 100%;
  }
  .technologiesPage .technologyContent .info .content aside {
    gap: 1.5rem;
    width: 100%;
  }
  .technologiesPage .technologyContent .info .content aside label {
    width: 3rem;
    height: 3rem;
  }
  .technologiesPage .technologyContent .info .content .techInfo span {
    font-size: 1rem;
  }
  .technologiesPage .technologyContent .info .content .techInfo .name {
    font-size: 2.2rem;
  }
  .technologiesPage .technologyContent .info .content .techInfo article {
    padding: 0 1rem;
  }
  .technologiesPage .technologyContent .info .content .techInfo article .about {
    font-size: 1rem;
  }
  .technologiesPage .technologyContent .imageCont {
    margin-top: 35%;
    height: 100%;
  }
}
