.homepage {
    background: url("../assets/home/background-home-desktop.jpg") no-repeat top fixed;
    background-size: cover;
    border: 1px transparent solid;
    height: 100vh;
    width: 100vw;

    .heroContent {
        margin: 15% auto 0;
        width: 100%;
        padding: 0 8rem 0 4rem;
        display: flex;
        justify-content: space-between;


        .heroText {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            width: 60%;

            .tagLine {
                font-family: 'Barlow Condensed', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 28px;
                line-height: 34px;
                letter-spacing: 4.725px;
                color: #D0D6F9;
            }

            .heroHead {
                font-family: 'Bellefair', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 150px;
                line-height: 172px;
                color: #FFFFFF;
            }

            .brief {
                font-family: 'Barlow', sans-serif;
                font-style: normal;
                font-size: 1.1rem;
                width: 70%;
                line-height: 32px;
                color: #D0D6F9;

            }
        }

        .buttonCont {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
            z-index: 1;
            position: relative;

            .heroButton {
                border: none;
                background-color: white;
                font-family: "Bellefair", sans-serif;
                font-size: 2.4rem;
                width: 15rem;
                height: 15rem;
                margin-right: 2rem;
                border-radius: 100%;
                position: relative;
                margin-bottom: 1rem;
                z-index: 1;
                cursor: pointer;

                &::after {
                    content: '';
                    position: absolute;
                    inset: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(255, 255, 255, .3);
                    border-radius: 100%;
                    transform: scale(0);
                    opacity: 0;
                    z-index: -1;
                    transition: transform 200ms ease-in-out, opacity 400ms ease-in;
                }

                &:hover::after {
                    opacity: 1;
                    transform: scale(1.4);
                }
            }
        }

    }
}


@media only screen and (max-width: 950px) {
    .homepage {
        background-image: url("../assets/home/background-home-tablet.jpg");

        .heroContent {
            flex-direction: column;
            justify-content: center;
            gap: 20rem;

            padding: 3rem 0;

            .heroText {
                width: 100%;
                align-items: center;
                text-align: center;

                .tagLine {
                    font-size: 1.5rem;
                }

                .brief {
                    margin: auto;
                    width: 60%;
                    font-size: 1rem;

                }
            }

            .buttonCont {
                padding: 0;
                width: 100%;
                justify-content: center;

                .heroButton {
                    margin: 0;
                }
            }

        }
    }
}

@media only screen and (max-width: 400px) {
    .homepage {
        background-image: url("../assets/home/background-home-mobile.jpg");

        .heroContent {
            margin-top: 20%;
            gap: 6rem;
            padding: 4rem 0;

            .heroText {
                gap: 0rem;

                .tagLine {
                    font-size: 1rem;
                    line-height: 1rem;
                    letter-spacing: 4px;

                }

                .heroHead {
                    margin-top: -1.3rem;
                    font-size: 5rem;
                    letter-spacing: 4px;
                }

                .brief {
                    width: 90%;
                    font-size: 1rem;
                    margin-top: -1rem;
                    line-height: 1.8rem;
                }
            }

            .buttonCont {

                .heroButton {

                    position: relative;
                    width: 12rem;
                    height: 12rem;
                    font-size: 2rem;
                }
            }
        }
    }
}